body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.card {
    height: 120px;
    margin: 20px 30px;
    position: relative;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
    _height: 60%;
    overflow: hidden;
    text-align: center;
}
.card > .img-container > img {
    _width: 100%;
    height: 120px;
    min-width: 200px;
}


.wrapper {
    _padding-top: 50px;
    background: #f1ef86;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    /*padding: 20px;*/
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}


.navbar li {
    display: inline-block;
    text-align: center;
}

.itemLeft {
    width: 13%;
}

.itemCenter {
    width: 50%;
    text-align: center;
}

.itemRight {
    width: 10%;
}

.navbar {
    background: #49baf1;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    width: 100%;
    position: fixed;
    margin: 0;
    z-index: 999;
}

li {
    color: white;
    padding: 15px 0;
}

body {
    font-size: 24px;
}
.score {
    margin:0 10px 0 0;
    background: #ffe500;
    border-radius: 5px;
    padding: 5px;
    float: right;
    width:95px;
    box-shadow: 0 3px 4px #b38c00;
}
.score-head {
    font-size: 15px;
    text-align: center;
}
.score-value {
    text-align: center;
    font-size: 40px;
    line-height: 40px;
}
.high-score {
    background: #d60000;
    border-radius: 5px;
    color: #fff;
    padding: 5px;
    box-shadow: 0 3px 4px #9a0101;
    float: right;
    width:95px;
}
